import React from 'react';

const Navigation = ({onratechange, isSignedIn}) => {
    if(isSignedIn){
        return (
        <nav style={{display: 'flex', justifyContent: 'flex-end'}}>
        <p onClick={() => onratechange('signout')} className='f3 link dim black underline pa3 pointer'>Sign Out</p>
        </nav>
        );
    }
    else
    {
        return (
        <nav style={{display: 'flex', justifyContent: 'flex-end'}}>
            <p onClick={() => onratechange('signin')} className='f3 link dim black underline pa3 pointer'>Sign In</p>
            <p onClick={() => onratechange('register')} className='f3 link dim black underline pa3 pointer'>Register</p>
        </nav>
        );
    }
}

export default Navigation;